.contact {
  display: flex;
  gap: 3em;
  align-items: center;
}

.logo {
  width: 3vw;
}

.resume {
  display: flex;
  flex-direction: column;
  gap: 3em;
  margin-top: 5em;
  line-height: 2;
}

.name {
  font-size: 3em;
}

.title {
  font-size: 1.5em;
}

.jobDescription a {
  color: blue;
  transition: color 0.2s linear;
}

.jobDescription a:hover {
  color: lightblue;
}

.experiences {
  display: flex;
  flex-direction: column;
  gap: 3em;
}